<template>
<v-app>
  <v-toolbar fixed flat class="mainToolbar">
    <v-container class="homeContainer">
      <v-layout class="rowz text-xs-center">
        <v-flex xs12>
          <div class="titl">
            <router-link class="titleMain" to="/">
              <h2>Milan Petrovic</h2>
            </router-link>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
  </v-toolbar>
  <v-container class="homeContainer margtop" grid-list-lg>
    <v-layout row wrap class="text-xs-center">
      <v-flex xs12 md6 v-for="pic in pics" :key="pic.id" @click="moveTop">
        <router-link :to="'/album/'+ pic.id">
          <clazy-load :src="getPic(pic.thumbnail)">
            <!-- The image slot renders after the image loads. -->
            <img :src="getPic(pic.thumbnail)" slot="image">
            <!-- The placeholder slot displays while the image is loading. -->
            <v-flex class="mt-5 text-xs-center" slot="placeholder" id="load">
              <v-progress-circular indeterminate color="gray" :width="4" :size="65"></v-progress-circular>
            </v-flex>
          </clazy-load>
        </router-link>
        <v-flex v-text="pic.text"></v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</v-app>
</template>
<script>
export default {
  data() {
    return {

    }
  },
  methods: {

    getPic(pic) {
      return require('@/assets/img/' + pic)
    },
    moveTop() {
      setTimeout(() => {
        window.scrollTo(0, 0)
      }, 200)
    }
  },
  computed: {
    pics() {
      return this.$store.getters.loadAlbums
    }
  }
}
</script>
<style>
.titleMain {
  position: relative;
  display: inline-block;
}

.titleMain:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
</style>



