import Home from '../components/home.vue'
import album from '../components/album.vue'
import contact from '../components/contact.vue'



export default [{
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/album/:id',
    name: 'album',
    props: true,
    component: album
  }
]



