<template lang="html">
  <v-app class="page">
    <v-container class="mainContainer margtop">
      <v-layout row class="text-xs-center pic mt-3" v-for="item in pic.items">
        <v-flex xs12>
          <clazy-load :src="getPic(item)">
          <!-- The image slot renders after the image loads. -->
            <img :src="getPic(item)" slot="image">
          <!-- The placeholder slot displays while the image is loading. -->
            <v-flex class="mt-5 text-xs-center" slot="placeholder" id="load">
              <v-progress-circular indeterminate color="gray" :width="4" :size="65"  ></v-progress-circular>
            </v-flex>
          </clazy-load>
          <v-flex v-text="pic.text"></v-flex>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  props:['id'],
  computed:{
    pic() {
      return this.$store.getters.LoadedAlbum(this.id)
    }
  },
  methods: {
    getPic(pic) {
      return require('@/assets/img/' + pic +'.jpg')
    }
  }

}
</script>

<style scoped lang="css">

</style>



